.burger-menu {
  display: flex;
  flex-direction: column;
  width: 36px;
  height: 13px;
  justify-content: space-between;
  position: relative;
  background-color: transparent;
  cursor: pointer;
  border: none;
}

.burger-menu:hover {
  opacity: 0.5;
  transition: 0.7s;
}

.burger-menu::before,
.burger-menu::after {
  position: absolute;
  content: "";
  height: 1px;
  width: 100%;
  background-color: white;

  @include tablets {
    transform: translateY(-2px);
  }
}

.burger-menu::before {
  top: 0;
  left: 0;
}

.burger-menu::after {
  bottom: 0;
  left: 0;
}
