.socials__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.socials__item {
  margin-right: 16px;

  &:last-child {
    margin-right: 0;
  }
}

.socials__link {
  display: block;
  color: transparent;
}

.socials__image {
  display: block;
  width: 27px;
  height: 27px;
  fill: #cdcdcd;
}

.socials__image--fb:hover {
  fill: #3578e5;
}

.socials__image--ok:hover {
  fill: #ee8208;
}

.socials__image--vk:hover {
  fill: #4a76a8;
}
